import React, { forwardRef, useEffect, useImperativeHandle, useMemo, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { Link } from 'react-router-dom';
import { useTasks } from './hooks';
import { IProps } from './NewTask';
import { InitState } from './store';
import { useImmer } from 'use-immer';

//antd
import { Table } from 'antd';
import type { ColumnsType, TableProps } from 'antd/lib/table';
import { findAllByLabelText } from '@testing-library/react';
interface Type {
  key: React.Key;
  fileName: string;
  size: string;

  downloadSpeed: string;
}

const columns: ColumnsType<Type> = [
  {
    title: '',
    dataIndex: 'fileName',
  },
  {
    title: '',
    dataIndex: 'size',
    sorter: {
      compare: (a, b) => Number(a.size) - Number(b.size),
      multiple: 3,
    },
  },

  {
    title: '',
    dataIndex: 'downloadSpeed',
    sorter: {
      compare: (a, b) => Number(a.downloadSpeed) - Number(b.downloadSpeed),
      multiple: 3,
    },
  },
];

function Stopped({ client }: IProps, ref: React.Ref<any>) {
  const onChange: TableProps<Type>['onChange'] = (pagination, filters, sorter, extra) => {
    console.log('params', pagination, filters, sorter, extra);
  };
  const [data, updateData] = useImmer<Type[]>([]);
  const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
  const dispatch = useDispatch();
  const tasks = useSelector((state: InitState) => {
    return state.stoppedTasks;
  });

  // selectedTasksGid[]
  //Maximum update depth exceeded.
  useEffect(() => {
    dispatch({
      type: 'updateSelectedTasksGid',
      gid: [],
    });
  }, []);

  const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
    setSelectedRowKeys(newSelectedRowKeys);
  };

  function onSelect(record: any, selected: any, selectedRows: any) {
    dispatch({
      type: 'updateSelectedTasksGid',
      gid: selectedRows.map((it: any) => {
        return tasks[it.key].gid;
      }),
    });
  }

  function onSelectAll() {
    dispatch({
      type: 'updateSelectedTasksGid',
      gid: tasks.map((task) => {
        return task.gid;
      }),
    });
  }

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
    onSelect,
    onSelectAll,
  };

  // <app />listComp<Header />
  // ref.current
  useImperativeHandle(ref, () => ({
    whatPage: 'stopped',
    clear: function (e: any) {
      setSelectedRowKeys(e);
    },
  }));

  useEffect(() => {
    // ;
    //@ts-ignore;
    client.tellStopped(0, 100).then((tasks) => {
      dispatch({
        type: 'updateStoppedTasks',
        tasks: tasks,
      });
      updateData((draft) => {
        tasks.forEach((task: any, idx: any) => {
          draft[idx] = {
            key: idx,
            fileName: task.files[0].path,
            size: task.totalLength,
            downloadSpeed: task.downloadSpeed,
          };
        });
      });
    });

    // 1s
    const id = setInterval(async () => {
      //@ts-ignore;
      const tasks = await client.tellStopped(0, 100);

      dispatch({
        type: 'updateStoppedTasks',
        tasks: tasks,
      });
      updateData((draft) => {
        // ,
        // tasks.forEach((task: any, idx: any) => {
        //   draft[idx] = {
        //     key: idx,
        //     fileName: task.files[0].path,
        //     size: task.totalLength,
        //     downloadSpeed: task.downloadSpeed,
        //   };
        // });
        draft.forEach((it: Type, idx: number) => {
          // ,
          if (!(idx in tasks)) {
            draft.splice(idx, 1);
          }
        });
      });
    }, 2000);

    return () => {
      clearInterval(id);
    };
  }, [client]);

  return (
    <div>
      <Table rowSelection={rowSelection} columns={columns} dataSource={data} onChange={onChange} />;
    </div>
  );
}

// 
export default forwardRef(Stopped);
