import EventEmitter from 'events';

// aria2.exeWebSocket
export default class Aria2Client extends EventEmitter {
  ws: WebSocket | null;

  id: number;
  // id
  callbacks: {
    [id: number]: (data: any) => void;
    // this.callbacks = {};
  } = {};
  readyPromise: Promise<Aria2Client>;
  constructor(public ip: string = '127.0.0.1', public port: number | string, public secret: number | string) {
    super();
    const url = `ws://${ip}:${port}/jsonrpc`;
    this.id = 1;
    this.ws = null;

    // ws,promise resolve(Aria2Client)
    this.readyPromise = new Promise((resolve, reject) => {
      // wsaria2
      // urlWebSocket
      this.ws = new WebSocket(url);
      // 
      this.ws.addEventListener('open', (e) => {
        // resolve
        resolve(this);
      });
      this.ws.addEventListener('error', (e) => {
        reject(this);
      });
    });

    // aria2message
    // @ts-ignore;
    this.ws.addEventListener('message', (e) => {
      // aria2
      const data = JSON.parse(e.data);
      const id = data.id;
      // id addUri...aria2
      if (id) {
        const callback = this.callbacks[id];
        callback(data);
        delete this.callbacks[id];
        // aria2onDownloadStart, onDownloadErrorid
      } else {
        const eventName = data.method.slice(8);
        this.emit(eventName, ...data.params);
      }
    });
  }

  // readyPromise fulfilled,
  ready() {
    return this.readyPromise;
  }
  destroy() {
    this.ws?.close();
  }
}

const aria2Methods = [
  'addUri',
  'addTorrent',
  'getPeers',
  'addMetalink',
  'remove',
  'pause',
  'forcePause',
  'pauseAll',
  'forcePauseAll',
  'unpause',
  'unpauseAll',
  'forceRemove',
  'changePosition',
  'tellStatus',
  'getUris',
  'getFiles',
  'getServers',
  'tellActive',
  'tellWaiting',
  'tellStopped',
  'getOption',
  'changeUri',
  'changeOption',
  'getGlobalOption',
  'changeGlobalOption',
  'purgeDownloadResult',
  'removeDownloadResult',
  'getVersion',
  'getSessionInfo',
  'shutdown',
  'forceShutdown',
  'getGlobalStat',
  'saveSession',
  // 'system.multicall',
  // 'system.listMethods',
  // 'system.listNotifications',
];

/**
 * Aria2Client.prototypearia2.exe;
 * @date 2022-06-08
 * @param {any} any[];
 * @returns {any} Promise resolve,rejectaria2data
 */
aria2Methods.forEach((methodName) => {
  // @ts-ignore 
  Aria2Client.prototype[methodName] = function (...args: any[]) {
    return this.ready().then(() => {
      return new Promise((resolve, reject) => {
        const id = this.id++;
        function callback(data: any) {
          if (data.error) {
            reject(data.error);
          } else {
            resolve(data.result);
          }
        }
        // 
        this.callbacks[id] = callback;
        // aria2.exejsonrpc
        // @ts-ignore
        this.ws.send(
          JSON.stringify({
            jsonrpc: '2.0',
            id,
            method: 'aria2.' + methodName,
            params: [`token:${this.secret}`, ...args],
          })
        );
      });
    });
  };
});
